import { Component, OnInit } from "@angular/core";
import { AdvancePayRate } from "@app/shared/models/advance-pay-rate.model";
import { SharedService } from "@app/shared/shared.service";
import { PayHistoryService } from "../payhistory.services";

@Component({
  selector: "app-advance",
  templateUrl: "./advance.component.html",
  styleUrls: ["./advance.component.css"],
})
export class AdvanceComponent implements OnInit {
  cols: any = [];
  employeeList: any;
  errorLabel: string;
  clientErrorMsg: any;
  selectedEmployee: any;
  advanceModel: string = "none";
  advancesPayRateList: any[] = [];
  employeeSelectedToaddAdvancePayRate: any;
  advancePayRateDetails: AdvancePayRate;

  constructor(
    private payHistoryService: PayHistoryService,
    private sharedService: SharedService
  ) {}

  ngOnInit(): void {
    this.cols = [
      { field: "employeeName", header: "Employee Name" },
      { field: "advancePaidDate", header: "Paid Date" },
      { field: "advanceAmount", header: "Amount" },
      { field: "description", header: "Description" },
      { field: "status", header: "Description" },
    ];
    this.advancePayRateDetails = new AdvancePayRate();
    this.getEmployees();
    this.sharedService.openToggleMenu("employeePayHistory");
  }
  getEmployees() {
    this.payHistoryService.getAllEmployees().subscribe((res) => {
      if (res.statusCode == 200) {
        this.employeeList = res.responsePayload;
        this.employeeList.splice(0, 0, {
          fullName: "Select Employee",
          value: null,
        });
      } else {
        this.employeeList = [];
      }
    });
  }
  showDialogToAdd() {
    this.advancePayRateDetails = new AdvancePayRate();
    this.advanceModel = "block";
    this.hiddenScroll();
  }
  showDialogToEdit(rowData) {
    this.advancePayRateDetails = rowData;
    this.advanceModel = "block";
    this.hiddenScroll();
  }
  getDetails(employee) {
    if (employee) {
      this.employeeSelectedToaddAdvancePayRate = employee;
      this.payHistoryService
        .getAdvances(employee.employeeDetailsId)
        .subscribe((res) => {
          if (res.statusCode == 200) {
            this.advancesPayRateList = res.responsePayload;
          } else {
            this.advancesPayRateList = [];
            this.sharedService.add({
              severity: "error",
              summary: "Error",
              detail: res.message,
            });
          }
        });
    } else {
      this.sharedService.add({
        severity: "error",
        summary: "Error",
        detail: "Please select employee",
      });
    }
  }
  selectEmployeeToAddAdvancePayRate() {
    this.advancePayRateDetails.employeeDetailsId = this.employeeSelectedToaddAdvancePayRate.employeeDetailsId;
  }
  save(form) {
    if (this.employeeSelectedToaddAdvancePayRate) {
      this.advancePayRateDetails.employeeDetailsId = this.employeeSelectedToaddAdvancePayRate.employeeDetailsId;
    }
    this.payHistoryService
      .saveAdvanceEmployeePayRate(this.advancePayRateDetails)
      .subscribe((res) => {
        if (res.statusCode == 200) {
          this.getDetails(this.selectedEmployee);
          this.closeUploadmodal();
          form.reset();
        } else {
          this.sharedService.add({
            severity: "error",
            summary: "Error",
            detail: res.message,
          });
        }
      });
  }
  statusUpdate(rowData, type) {
    if (type == "activate") {
      this.payHistoryService
        .activateAdvancePayRate(rowData.employeeAdvanceDetailsId)
        .subscribe((res) => {
          if (res.statusCode == 200) {
            this.getDetails(rowData);
            this.sharedService.add({
              severity: "success",
              summary: "Success",
              detail: res.message,
            });
          }
        });
    } else if (type == "deactivate") {
      this.payHistoryService
        .deactivateAdvancePayRate(rowData.employeeAdvanceDetailsId)
        .subscribe((res) => {
          if (res.statusCode == 200) {
            this.getDetails(rowData);
            this.sharedService.add({
              severity: "success",
              summary: "Success",
              detail: res.message,
            });
          }
        });
    }
  }
  hiddenScroll() {
    try {
      let bodyElement = document.getElementById("modalbody") as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.add("modal-open");
      }
    } catch (ex) {
      this.clientErrorMsg(ex, "hiddenScroll");
    }
  }
  displayScroll() {
    try {
      let bodyElement = document.getElementById("modalbody") as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.remove("modal-open");
      }
    } catch (ex) {
      this.clientErrorMsg(ex, "displayScroll");
    }
  }
  closeUploadmodal() {
    this.advanceModel = "none";
    this.displayScroll();
  }
}
