import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { AuthService } from '@app/core/service/auth.service';
import { ExpancesPayRate, ExpancesPayRateItemList } from '@app/shared/models/expences-pay-rate.model';
import { SharedService } from '@app/shared/shared.service';
import { PayHistoryService } from '../payhistory.services';

@Component({
  selector: 'app-expences',
  templateUrl: './expences.component.html',
  styleUrls: ['./expences.component.css']
})
export class ExpencesComponent implements OnInit {
  cols: any = [];
  employeeList: any;
  errorLabel: string;
  clientErrorMsg: any;
  selectedEmployee: any;
  expenseModel: string = "none";
  expensesPayRateList: any[] = [];
  expensesEntries: any[] = [];
  descriptionOption: any = [];
  employeeSelectedToaddexpensesPayRate: any
  expensesPayRateDetails: ExpancesPayRate
  organizationDetailsId: any


  constructor(private payHistoryService: PayHistoryService, private sharedService: SharedService, private authService: AuthService, private cdr: ChangeDetectorRef,){ }

  ngOnInit(): void {
    this.organizationDetailsId = this.authService.currentUser.organizationDetailsId;
    this.cols = [
      { field: "employeeName", header: "Employee Name" },
      { field: "expenseName", header: "Expanse Name" },
      { field: "expenseDate", header: "Expanse Date" },
      { field: "status", header: "Status" },
    ];
    this.expensesPayRateDetails = new ExpancesPayRate();
    this.getEmployees();
    this.onAddexpenseLineItem();
    this.sharedService.openToggleMenu('employeePayHistory')  
  }
  getEmployees() {
    this.payHistoryService.getAllEmployees().subscribe((res) => {
      if (res.statusCode == 200) {
        this.employeeList = res.responsePayload;
        this.employeeList.splice(0, 0, {
          fullName: "Select Employee",
          value: null,
        });
      } else {
        this.employeeList = [];
      }
    });
  };
  showDialogToAdd() {
    this.expensesPayRateDetails = new ExpancesPayRate();
    this.expensesEntries = [];
    this.onAddexpenseLineItem()
    this.getDescriptionList(this.organizationDetailsId);
    this.expenseModel = "block";
    this.hiddenScroll();
  }
  showDialogToEdit(rowData) {
    let details= rowData
    details.expenseLineItems.forEach(element => {
      element.descriptionType= {label:element.description,value:element.description}       
     });
      this.expensesPayRateDetails = details
      this.expensesEntries = details.expenseLineItems
    this.getDescriptionList(this.organizationDetailsId);
    this.expenseModel = "block";
    this.hiddenScroll();
  }
  getDetails(employee) {
    if (employee  ) {
      this.employeeSelectedToaddexpensesPayRate = employee
      this.payHistoryService.getExpenses(employee.employeeDetailsId).subscribe((res) => {
        if (res.statusCode == 200) {
          this.expensesPayRateList = res.responsePayload;
        } else {
          this.sharedService.add({
            severity: "error",
            summary: "Error",
            detail: res.message,
          });
        }
      });
    } else {
      this.sharedService.add({
        severity: "error",
        summary: "Error",
        detail: 'Please select employee',
      });
    }
  }
  getDescriptionList(organizationDetailsId) {
    this.payHistoryService.getExpensesDescription(organizationDetailsId).subscribe(async (res) => {
      if (res.statusCode == 200) {
        let list = []
        await res.responsePayload.forEach(element => {
          list.push({ label: element, value: element });
        });
        list.splice(0, 0, {
          label: "Select Description",
          value: null,
        });
        list.push({ label: 'Other', value: 'Other' });
       this.descriptionOption= list
       this.cdr.detectChanges();
      } else {
        this.descriptionOption = [];
        this.sharedService.add({
          severity: "error",
          summary: "Error",
          detail: res.message,
        });
      }
    });
  }
  selectEmployeeToAddexpensesPayRate() {
    this.expensesPayRateDetails.employeeDetailsId = this.employeeSelectedToaddexpensesPayRate.employeeDetailsId
  }
  save(form) {

    if (this.employeeSelectedToaddexpensesPayRate) {
      this.expensesPayRateDetails.employeeDetailsId = this.employeeSelectedToaddexpensesPayRate.employeeDetailsId
    }
    this.expensesPayRateDetails.expenseLineItems = this.expensesEntries
    this.expensesEntries.forEach(element => {
      if (element.descriptionType.value != 'Other') {
        element.description = element.descriptionType.value
      }
    });
    this.payHistoryService.saveExpencesEmployeePayRate(this.expensesPayRateDetails).subscribe(res => {
      if (res.statusCode == 200) {
        this.getDetails(this.selectedEmployee)
        this.closeUploadmodal()
        form.reset()
      } else {
        this.sharedService.add({
          severity: "error",
          summary: "Error",
          detail: res.message,
        });
      }
    })
  }
  onAddexpenseLineItem() {
    let expenseLineIteamDetails = Object.assign({}, new ExpancesPayRateItemList());
    this.expensesEntries.push(expenseLineIteamDetails);
  }
  onRemoveexpenseLineItem(expense, index) {
    if (this.expensesEntries.length > 1) {
      if (expense.employeeExpenseLineItemDetailsId) {
        this.expensesEntries[index].status = '0'
      } else {
        this.expensesEntries.splice(index, 1);
      }
    }
    this.cdr.detectChanges()
  }
  onResetDescription(expense, i){
    this.expensesEntries[i].descriptionType={
      label: "Select Description",
      value: null,
    }
  }
  statusUpdate(rowData, type) {
    if(type=='activate'){
      this.payHistoryService.activateExpensePayRate(rowData.employeeExpenseDetailsId).subscribe(res=>{
        if(res.statusCode==200){
          this.getDetails(rowData);
          this.sharedService.add({
            severity: "success",
            summary: "Success",
            detail: res.message,
          });
        }
      })

    }else if(type=='deactivate'){
      this.payHistoryService.deactivateExpensePayRate(rowData.employeeExpenseDetailsId).subscribe(res=>{
        if(res.statusCode==200){
          this.getDetails(rowData);
          this.sharedService.add({
            severity: "success",
            summary: "Success",
            detail: res.message,
          });
        }
      })
    }
   
  }
  hiddenScroll() {
    try {
      let bodyElement = document.getElementById("modalbody") as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.add("modal-open");
      }
    } catch (ex) {
      this.clientErrorMsg(ex, "hiddenScroll");
    }
  }
  displayScroll() {
    try {
      let bodyElement = document.getElementById("modalbody") as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.remove("modal-open");
      }
    } catch (ex) {
      this.clientErrorMsg(ex, "displayScroll");
    }
  }
  closeUploadmodal() {
    this.expenseModel = "none";
    this.displayScroll();
  }

}
