import { Component, OnInit } from '@angular/core';
import { PayRate } from '@app/shared/models/payRate.model';
import { SharedService } from '@app/shared/shared.service';
import { PayHistoryService } from '../payhistory.services';

@Component({
  selector: 'app-payrate',
  templateUrl: './payrate.component.html',
  styleUrls: ['./payrate.component.css']
})
export class PayrateComponent implements OnInit {
  cols: any = [];
  payRateList: PayRate[]=[];
  selectedEmployee: any;
  employeeSelectedToaddPayRate: any;
  employeeList: any;
  payRateModel: string = "none";
  errorLabel: string;
  clientErrorMsg: any;
  payRateDetails: PayRate;
  payrateMode: any;
  selectedpayrateMode: any = "Rate";
  constructor(private payHistoryService:PayHistoryService,private sharedService:SharedService) {}

  ngOnInit(): void {
    this.cols = [
      { field: "employeeName", header: "Employee Name" },
      { field: "rate", header: "Rate" },
      { field: "salary", header: "Salary" },
      { field: "startDate", header: "Start Date" },
      { field: "endDate", header: "End Date" },
      { field: "rateCodeDescription", header: "Description" },
      { field: "status", header: "Status" },
    ];
    this.payrateMode = [
      { label: "Rate", value: "1" },
      { label: "Salary", value: "2" },
    ];
    this.payRateDetails = new PayRate();
    this.getEmployees()
    this.sharedService.openToggleMenu('employeePayHistory')  
  }
  getEmployees(){
    this.payHistoryService.getAllEmployees().subscribe((res) => {
      if (res.statusCode == 200) {
        this.employeeList = res.responsePayload;
        this.employeeList.splice(0, 0, {
          fullName: "Select Employee",
          value: null,
        });
      } else {
        this.employeeList = [];
      }
    });
  };
  getDetails(employee) {
    if(employee){
    this.employeeSelectedToaddPayRate= employee
      this.payHistoryService.getPayRate(employee.employeeDetailsId).subscribe((res) => {
        if (res.statusCode == 200) {
          this.payRateList = res.responsePayload;
        } else {
          this.payRateList = [];
          this.sharedService.add({
            severity: "error",
            summary: "Error",
            detail: res.message,
          });
        }
      });
    }else{
      this.sharedService.add({
        severity: "error",
        summary: "Error",
        detail: 'Please select employee',
      });
    }
  }
  statusUpdate(rowData, type) {
    if(type=='activate'){
      this.payHistoryService.activatePayRate(rowData.employeePayRateDetailsId).subscribe(res=>{
        if(res.statusCode==200){
          this.getDetails(rowData);
          this.sharedService.add({
            severity: "success",
            summary: "Success",
            detail: res.message,
          });
        }
      })

    }else if(type=='deactivate'){
      this.payHistoryService.deactivatePayRate(rowData.employeePayRateDetailsId).subscribe(res=>{
        if(res.statusCode==200){
          this.getDetails(rowData);
          this.sharedService.add({
            severity: "success",
            summary: "Success",
            detail: res.message,
          });
        }
      })
    }
   
  }
  savePayRate(form) {
    if(this.payRateDetails.payRateType =='1'){
      this.payRateDetails.rateOrSalary= this.payRateDetails.rate
    }else{
      this.payRateDetails.rateOrSalary = this.payRateDetails.salary
    }
    this.payHistoryService.saveEmployeePayRate(this.payRateDetails).subscribe(res=>{
      if(res.statusCode==200){
        this.getDetails(this.selectedEmployee)
        this.closeUploadmodal()
        form.reset()
      }else{
        this.sharedService.add({
          severity: "error",
          summary: "Error",
          detail: res.message,
        });
      }
    })
  }
  selectEmployeeToAddPayRate(){
    this.payRateDetails.employeeDetailsId= this.employeeSelectedToaddPayRate.employeeDetailsId
  }
  checkPayRateExists(){
    if(this.employeeSelectedToaddPayRate){
      this.payRateDetails.employeeDetailsId= this.employeeSelectedToaddPayRate.employeeDetailsId
      this.payHistoryService.isPayRateExists(this.payRateDetails.employeeDetailsId,this.payRateDetails.startDate).subscribe(res=>{
        if(res.statusCode!=200){
          this.errorLabel= res.message;
          this.payRateDetails.startDate=null;
        }else{
          this.errorLabel='';
        }
      })
    }else{
      this.errorLabel = 'Please select Employee'
      this.payRateDetails.startDate=null;
    }    
  }
  showDialogToAdd() {
    this.payRateModel = "block";
    this.payRateDetails = new PayRate();
    this.hiddenScroll();
  }
  showDialogToEdit(rowData) {
    this.payRateModel = "block";
    this.payRateDetails = rowData
    this.hiddenScroll();
  }
  hiddenScroll() {
    try {
      let bodyElement = document.getElementById("modalbody") as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.add("modal-open");
      }
    } catch (ex) {
      this.clientErrorMsg(ex, "hiddenScroll");
    }
  }
  displayScroll() {
    try {
      let bodyElement = document.getElementById("modalbody") as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.remove("modal-open");
      }
    } catch (ex) {
      this.clientErrorMsg(ex, "displayScroll");
    }
  }
  closeUploadmodal() {
    this.payRateModel = "none";
    this.payRateDetails = new PayRate();
    this.displayScroll();
  }

}
